.stats-main{
  border:0px solid blue;
  display:flex;
  flex-direction: column;
  margin-top: 70px;

}
/* <---- Desktop (more than 821px) ----->*/
#first,#second,#third{
  width:50%;
  border:0px solid red;
  margin:auto;
  margin-bottom:40px;
}

@media screen and (max-width:1024px){
 
  #first,#second,#third{
      width:40%;
      border:0px solid red;
      margin:auto;
      margin-bottom:40px;
  }
  /* .stats-main img:nth-child(-n+2) */
  
}

/* <----- Tablet(600px to 820px) -----> */

@media screen and (max-width:600px){
  #first,#second,#third{
      width:70%;
      border:0px solid red;
      margin:auto;
      margin-bottom:30px;
  }
} 

